        <div class="resume-left">
          <h3>Qui suis-je ?</h3>
          <p>
            Passionné depuis très jeune par la photo et la vidéo, je me définis comme un créatif ayant développé
            un
            intérêt
            particulier pour la programmation au cours des années. C’est donc dans le graphic design et le
            développement
            front-end que j’ai su trouver mon parfait équilibre. Je peux ainsi combiner ma technicité avec ma
            sensibilité
            visuelle et créative afin de créer des interfaces intuitives et esthétiques dont je suis fier.
            Donner vie à
            des
            idées au travers de collaborations inspirantes et stimulantes est pour moi une mission qui m’anime
            et que je
            souhaite mener le plus longtemps possible.
          </p>

